@use './src/globalStyles/mixins' as *;
@use './src/globalStyles/variables' as *;

.Choice {
  @include adaptive-value('padding-top', 100, 25, 0);
  background-color: $bgc;
  color: $color3;

  .ChoiceBody {
    & > h2 {
      @include adaptive-value('margin-bottom', 30, 10, 0);
    }
    & > .ChoiceText__Row {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      row-gap: 20px;
      column-gap: 20px;
      @include adaptive-value('margin-bottom', 30, 10, 0);
      & > .row__item {
        cursor: pointer;
        flex-basis: 270px;

        @include adaptive-value('margin-bottom', 25, 15, 0);
        & > img {
          width: 100%;
          @include adaptive-value('margin-bottom', 20, 10, 0);
        }
        & > p, & > b {
          @include adaptive-value('font-size', 18, 16, 0);
        }
        & > p {
          @include adaptive-value('margin-bottom', 10, 5, 0);
        }
        & > b {
          display: inline-block;
          @include adaptive-value('margin-bottom', 25, 10, 0);
        }
        & > .buttons {
          display: flex;

          & > button {
            transition: all .3s ease;
            &:hover {
              opacity: .8;
            }
            &:active {
              opacity: .6;
            }

          }

          & > button:first-child {
            border: 1px solid #BCBCBC;
            border-radius: 30px 0 0 30px;
            @include adaptive-value('height', 50, 40, 0);
            width: 65px;
            margin-right: 10px;
            & > svg path {
              stroke: $color3;
            }
          }
          & > button:last-child {
            @include adaptive-value('height', 50, 40, 0);
            width: 100%;
            border: 1px solid #333333;
            border-radius: 0 30px 30px 0;
            @include adaptive-value('padding-left', 25, 5, 0);
            @include adaptive-value('padding-right', 25, 5, 0);
            font-weight: 600;
            display: flex;
            justify-content: space-between;
            align-items: center;
          }
        }
      }
    }
  }
  .tape {
    @include adaptive-value('height', 250, 150, 0);
    position: relative;
    overflow: hidden;

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 30px;
      background-color: $bgcApp;
    }

    & > div {
      position: absolute;
      padding: 18px 0;
      left: -10%;
      right: -10%;
      top: 40%;
      text-transform: uppercase;
      white-space: nowrap;
      display: flex;
      justify-content: space-between;

      &:first-child {
        transform: rotate(5deg);
        color: $color;
        z-index: 2;
        background-color: #0178F3;
        @media (max-width: 678px) {
          transform: rotate(10deg);
        }
        & > span {
          @include adaptive-value('margin-right', 150, 5, 0);
        }
      }
      &:last-child {
        transform: rotate(-2.5deg);
        color: $color3;
        border: 1px solid $color3;
        @media (max-width: 678px) {
          transform: rotate(-5deg);
        }
        & > span {
          @include adaptive-value('margin-right', 190, 10, 0);
        }
      }
    }
  }
}
