@use './src/globalStyles/mixins' as *;


.SomeBlock {
  @include adaptive-value('padding-top', 50, 30, 0);
  @include adaptive-value('margin-bottom', 150, 80, 0);
  h2 {
    margin-bottom: 20px;
  }
  p {
    margin-bottom: 10px;
  }
  li {
    margin-bottom: 5px;
    &:before {
      content: '- ';
    }
  }
}