@use './src/globalStyles/variables' as *;
@use './src/globalStyles/mixins' as *;

.HeaderBody {
  display: flex;
  justify-content: space-between;
  align-items: center;
  @include adaptive-value('padding-top', 25, 12, 0);
  @include adaptive-value('padding-bottom', 25, 12, 0);

  color: $color2;


  & .row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;

    &:nth-child(1) {
      width: 120px;

      div a {
        padding: 0 4px;
      }
    }

    &:nth-child(2) a:not(:last-child) {
      padding-right: 25px;
    }

    &:nth-child(3) {
      & > a {
        border: 2px solid $color;
        padding: 12px;
        border-radius: 50%;
      }

      div > a {
        padding-right: 15px;
      }
    }

    &:nth-child(4) {
      display: none;
    }
  }
}

footer {
  background-color: #fff;
  color: $color3;
  @include adaptive-value('padding', 100, 40, 0);
  padding-left: 0;
  padding-right: 0;
}

.Footer {


  & > div:not(:first-child) {
    display: flex;
    justify-content: space-between;
  }
  & .Footer__logoAndLinks {
    display: flex;
    align-items: center;
    @include adaptive-value('margin-bottom', 50, 30, 0);
    & > img {
      @include adaptive-value('margin-right', 50, 30, 0);
      @include adaptive-value('width', 260, 160, 0);
    }
    & div > a:first-child {
      @include adaptive-value('margin-right', 15, 8, 0);
    }
  }
  & > div {
    display: flex;
    flex-wrap: wrap-reverse;
    & .Footer__about {
      @include adaptive-value('width', 340, 300, 0);
      @include adaptive-value('margin-right', 160, 0, 0);

      p {
        color: $color5;
        line-height: 22px;
      }
      span {
        line-height: 22px;
      }
      a {
        text-decoration: underline;
      }

      div {
        @include adaptive-value('margin-bottom', 15, 12, 0);
      }

      & .contacts {
        display: flex;
        width: 100%;
        flex-wrap: wrap;
        justify-content: space-between;
      }
    }
    & .Footer__allAbout {
      display: flex;
      justify-content: space-between;
      flex: 1 1 auto;
      @include adaptive-value('padding-right', 50, 0, 0);
      position: relative;
      margin-bottom: 20px;
      & > .column {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex: 1 1 auto;
        &:not(:last-child) {
          margin-right: 25px;
        }
         & > div {
           h3 {
             font-size: 18px;
             font-weight: 700;
             @include adaptive-value('margin-bottom', 25, 15, 0);
           }
           a {
             display: block;
             @include adaptive-value('margin-bottom', 15, 10, 0);
             transition: all .3s ease;
             &:hover {
               text-decoration: underline;
             }
             &:active {
               opacity: .8;
             }
           }
         }
      }

      .company {
        position: absolute;
        @include adaptive-value('bottom', 15, -35, 0);
        right: 0;
        & img {
          @include adaptive-value('width', 135, 80, 0);
        }
      }
      .support {
        border: 1px solid #F64F00;
        color: #F64F00;
        border-radius: 50%;
        @include adaptive-value('height', 150, 60, 0);
        @include adaptive-value('width', 150, 60, 0);
        @include adaptive-value('font-size', 15, 8, 0);
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        position: absolute;
        @include adaptive-value('top', -80, -25, 0);
        right: 0;
      }
    }
  }
}

@media (max-width: 1024px) {
  .HeaderBody {
    .row:not(:first-child) {
      display: none;
    }
    .row:last-child {
      display: block;

      & > div {
        display: flex;
        width: 20px;
        height: 15px;
        justify-content: space-between;
        flex-direction: column;
        & > span {
          background-color: $color;
          height: 1px;
        }
      }
    }
  }
}
