@use './src/globalStyles/mixins' as *;
@use './src/globalStyles/variables' as *;


.swiper {
  @include adaptive-value('margin-bottom', 80, 30, 0);
  @include adaptive-value('margin-top', 30, 15, 0);
  @include adaptive-value('height', 600, 185, 0);
  overflow: visible;

  .swiper-wrapper {
  }

  .swiper-slide {
    cursor: pointer;
    width: 100%;
    text-align: center;
    font-size: 18px;
    transition: all .3s ease;

    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;

    & > img {
      transition: all .3s ease;
      position: absolute;
      bottom: 0;
      height: 80%;
      border-radius: 100px 0;
      display: block;
      object-fit: cover;
    }
  }

  .swiper-slide-active {
    &> img {
      height: 100%;
      background-color: #fff;
      @include adaptive-value('border-radius', 100, 30, 0);
      border-bottom-left-radius: 0;
      border-top-right-radius: 0;
    }
  }

  .swiper-slide-prev > img {
    border-radius: 0 100px;
  }

  .swiper-button-prev, .swiper-button-next {
    border: 1px solid $color2;
    position: absolute;
    height: 5.5vh;
    width: 3.5vw;
    min-width: 25px;
    transition: opacity .3s ease;

    &:after {
      @include adaptive-value('font-size', 15, 10, 0);
      color: $color2;
    }
  }

  .swiper-button-prev {
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
    @include adaptive-value('left', 370, 5, 0);
  }

  .swiper-button-next {
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    @include adaptive-value('right', 370, 5, 0);
  }

  .SliderBody {
    position: absolute;
    z-index: 2;
    @include adaptive-value('right', 60, 30, 0);
    @include adaptive-value('top', 40, 20, 0);
    @include adaptive-value('width', 250, 120, 0);
    text-align: left;

    & > a {
      display: flex;
      position: absolute;
      @include adaptive-value('height', 120, 60, 0);
      @include adaptive-value('width', 120, 60, 0);
      border-radius: 50%;
      align-items: center;
      justify-content: center;
      @include adaptive-value('top', -120, -55, 0);
      @include adaptive-value('right', -40, -20, 0);
      background-color: $color;
      color: $color3;
      transform: rotate(15deg);
      font-weight: 600;
      &:hover {
        filter: brightness(85%);
        transform: scale(1.1);
      }

      &:active {
        filter: brightness(70%);
      }
    }
    & > h3 {
      font-weight: 600;
      @include adaptive-value('font-size', 24, 16, 0);
      margin-bottom: 10px;
    }
    & > span {
      @include adaptive-value('padding-right', 70, 0, 0);
      @include adaptive-value('margin-bottom', 30, 10, 0);
      display: inline-block;
      opacity: .7;
      @include adaptive-value(' line-height', 22, 13, 0);
    }
    transition: all .3s ease;
    opacity: 0;
  }

  .swiper-slide-active > .SliderBody {
    opacity: 1;
  }
}
