@use './src/globalStyles/mixins' as *;

.HomeBody {
  @include adaptive-value('margin-top', 28, 0, 0);
  height: 100%;

  & > img {
    width: 70vw;
    max-width: 1100px;
    display: block;
    margin: 0 auto;
  }
  &:before {
    content: '';
    position: absolute;
    @include adaptive-value('top', 90, 90, 0);
    @include adaptive-value('left', 110, 60, 0);
    @include adaptive-value('height', 700, 90, 0);
    @include adaptive-value('width', 700, 90, 0);
    background: radial-gradient(50% 50% at 50% 50%,
            rgba(246, 79, 0, 0.92) 0%,
            rgba(246, 79, 0, 0) 100%);
    opacity: .7;
    filter: blur(32px);
    z-index: 0;
    animation: circle1 ease 6s infinite;
  }
  &:after {
    content: '';
    position: absolute;
    @include adaptive-value('top', 100, 140, 0);
    @include adaptive-value('right', 100, 50, 0);
    @include adaptive-value('height', 800, 100, 0);
    @include adaptive-value('width', 800, 100, 0);
    background: radial-gradient(50% 50% at 50% 50%,
            #0178F3 0%, rgba(1, 120, 243, 0) 100%);
    opacity: .7;
    filter: blur(32px);
    z-index: 0;
    animation: circle2 ease 10s infinite;
  }
}

@keyframes circle1 {
  0%, 100% {
    filter: blur(32px);
    opacity: 1;
    transform: translate(0);
  }
  50% {
    filter: blur(60px);
    opacity: .3;
    transform: translate(-20%);
  }
}

@keyframes circle2 {
  0%, 100% {
    filter: blur(32px);
    opacity: 1;
    transform: translate(0);
  }
  50% {
    filter: blur(60px);
    opacity: .5;
    transform: translate(20%);
  }
}