@import "./globalStyles/index";
@import "./globalStyles/variables";
@import "./globalStyles/mixins";

.App {
  overflow-x: hidden;
}

.Container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 0.8rem;
}

section {
}

h2 {
  display: inline-block;
  @include adaptive-value('font-size', 48, 26, 0);
  font-weight: 700;
}

p, span, a, button, b {
  @include adaptive-value('font-size', 16, 10, 0);
}

a {
  display: inline-block;
  transition: all .3s ease;
  &:hover {
    filter: brightness(120%);
  }
  &:active {
    filter: brightness(80%);
  }
  &.active {
    color: $color;
  }
}


