@use './src/globalStyles/variables' as *;
@use './src/globalStyles/mixins' as *;

:root {
  --noOfItems: 27;
}

.Circle {
  @include adaptive-value('height', 110, 60, 0);
  @include adaptive-value('width', 110, 60, 0);
  position: relative;
  
  & > img {
    margin-left: 2px;
    position: absolute;
    height: 20px;
    width: 20px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 5;
  }
  & > h2 {
    height: 100%;
    width: 100%;
    border: 1px solid $color;
    position: relative;
    text-align: center;
    color: $color;
    border-radius: 50%;
    animation: circle linear 50s infinite;

    & > span {
      @include adaptive-value('font-size', 10, 4, 0);
      --units: 1;
      --rotationUnit: calc((1turn / var(--noOfItems)) * var(--units, 1));
      position: absolute;
      width: calc(100% - 0.8rem);
      height: calc(100% - 0.8rem);
      top: 0.4rem;
      left: 0.4rem;
      transform: rotate(var(--rotationUnit));
      transform-origin: center;

      &:nth-child(2) { --units: 2; }
      &:nth-child(3) { --units: 3; }
      &:nth-child(4) { --units: 4; }
      &:nth-child(5) { --units: 5; }
      &:nth-child(6) { --units: 6; }
      &:nth-child(7) { --units: 7; }
      &:nth-child(8) { --units: 8; }
      &:nth-child(9) { --units: 9; }
      &:nth-child(10) { --units: 10; }
      &:nth-child(11) { --units: 11; }
      &:nth-child(12) { --units: 12; }
      &:nth-child(13) { --units: 13; }
      &:nth-child(14) { --units: 14; }
      &:nth-child(15) { --units: 15; }
      &:nth-child(16) { --units: 16; }
      &:nth-child(17) { --units: 17; }
      &:nth-child(18) { --units: 18; }
      &:nth-child(19) { --units: 19; }
      &:nth-child(19) { --units: 19; }
      &:nth-child(20) { --units: 20; }
      &:nth-child(21) { --units: 21; }
      &:nth-child(22) { --units: 22; }
      &:nth-child(23) { --units: 23; }
      &:nth-child(24) { --units: 24; }
      &:nth-child(25) { --units: 25; }
      &:nth-child(26) { --units: 26; }
      &:nth-child(27) { --units: 27; }
    }
  }
}

@keyframes circle {
  0% {
    transform: rotate(0);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}